<template>
   <div class="time">
      <div class="title">
         <h1 :style="{opacity:opacity}">兰鑫，生日快乐！！！</h1>
      </div>
      <div class="heart" @click="changBox"></div>
      <transition 
         appear
         name="animate__animated animate__bounce"
         enter-active-class="animate__backInDown"
         leave-active-class="animate__backOutUp">
         <div id="box" v-show=show><br/>
         这个世界有你：{{birthTimeNow|dateForm}}<br/><br/><br/>
         我已经遇见你：{{meetTimeNow|dateForm}}
         </div>
      </transition>
      <transition
         appear
         name="animate__animated animate__bounce"
         enter-active-class="animate__backInDown"
         leave-active-class="animate__backOutUp">
      <div id="box" v-show=!show><br/>
         我们在一起已经：{{loveTimeNow|dateForm}}<br/><br/><br/>
         祝宝贝生日快乐
      </div>
      </transition>
   </div>
</template>
<script>
import moment from 'moment';
let nowTime = moment().format("YYYY-MM-DD HH:mm:ss")
let birthTimeNow = moment(nowTime).diff(moment("2001-10-14 00:00:00"), "seconds")
let meetTimeNow = moment(nowTime).diff(moment("2017-09-01 00:00:00"), "seconds")
let loveTimeNow = moment(nowTime).diff(moment('2018-01-01 00:00:00'), "seconds")
export default {
   name: 'Footer',
   data() {
      return {
         nowTime,
         meetTimeNow,
         loveTimeNow,
         birthTimeNow,
         show:true,
         opacity:1
      };
   },
   filters: {
      dateForm: function (second) {
         var d = moment.duration(second, 'seconds');
        return Math.floor(d.asDays()) + '天' + d.hours() + '时' + d.minutes() + '分' + d.seconds() + '秒'
      }
   },
   methods: {
      changBox() {
         this.show =!this.show
      }
   },
   mounted() {
      let timer = setInterval(() => {
         this.birthTimeNow++
         this.meetTimeNow++
         this.loveTimeNow++
      }, 1000);
      setInterval(() => {
         if(this.opacity<0.1){
            this.opacity+=1
         }
         this.opacity-=0.1
      }, 200);
   }
}
</script>

<style scoped>
.time {
   margin: auto;
   height: 90vh;
   width: 100%;
   background: url(@/assets/bgc.webp);
   background-size: 100% 100%;
   background-repeat: no-repeat;
}

.title {
   padding-top: 100px;
   margin-left: 20%;

}

.title h1 {
   text-align: center;
   vertical-align: auto;
   font-size: 80px;
   color: red;
}

.heart {
   position: relative;
   left: 50%;
}

.heart:hover {
   cursor: pointer;
   opacity: 0.5;
}

.heart:before,
.heart:after {
   content: "";
   width: 70px;
   height: 115px;
   position: absolute;
   background: red;
   left: 70px;
   top: 0;
   -webkit-border-radius: 50px 50px 0 0;
   -moz-border-radius: 50px 50px 0 0;
   border-radius: 50px 50px 0 0;
   -webkit-transform: rotate(-45deg);
   -moz-transform: rotate(-45deg);
   -ms-transform: rotate(-45deg);
   -o-transform: rotate(-45deg);
   transform: rotate(-45deg);
   -webkit-transform-origin: 0 100%;
   -moz-transform-origin: 0 100%;
   -ms-transform-origin: 0 100%;
   -o-transform-origin: 0 100%;
   transform-origin: 0 100%;
}

.heart:after {
   left: 0;
   -webkit-transform: rotate(45deg);
   -moz-transform: rotate(45deg);
   -ms-transform: rotate(45deg);
   -o-transform: rotate(45deg);
   transform: rotate(45deg);
   -webkit-transform-origin: 100% 100%;
   -moz-transform-origin: 100% 100%;
   -ms-transform-origin: 100% 100%;
   -o-transform-origin: 100% 100%;
   transform-origin: 100% 100%;
}

#box {
   text-align: center;
   position: absolute;
   width: 50%;
   height: 35%;
   color: black;
   margin-left: 30%;
   bottom: 10%;
   background-color: rgb(255, 246, 247);
   font-size: 30px;
}
</style>